*{box-sizing:border-box}body{margin:0;font-family:Proxima Nova, sans-serif;padding:0;color:#333D51}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{max-width:1110px;min-width:320px;margin:0 auto}h1,h2,h3,h4,h5,p{margin:0;padding:0}ul,li,menu{display:block;margin:0;padding:0}a{text-decoration:none;color:#333D51}.line-blue{position:relative}.line-blue:before{content:'';width:2px;height:100%;background-color:#004FEC;position:absolute;left:-25px;top:0px}

.wrapper-header{display:flex;align-items:center;justify-content:space-between}.header{padding:45px 0}.menu-header{display:flex;width:285px;justify-content:space-between}.logo{font-weight:bold;font-size:25px;line-height:30px}.menu__item{font-size:18px;line-height:22px}.menu__link{transition:all .3s ease}.menu__link:focus,.menu__link:hover{color:#004FEC}

.main{padding:90px 0}.title{margin-bottom:100px;text-transform:uppercase;font-weight:normal;font-size:60px;line-height:93%}.title span.title-span-block{display:block;margin-bottom:15px}.title span.color-active{color:#004FEC;margin-left:22px}.wrapper-post{display:flex;flex-wrap:wrap}.allNews{margin-top:45px}.allNews__link{color:#004FEC;font-size:20px;line-height:24px;padding-bottom:3px;transition:all .2s ease;border-bottom:2px solid transparent}.allNews__link:hover{border-bottom:2px solid #004FEC}

.post{max-width:350px;padding:0px 25px 55px 25px;height:290px;display:flex;flex-direction:column}.link-bloc{display:flex;justify-content:space-between}.post__title{font-size:25px;line-height:30px;max-width:300px;position:relative;transition:all .5s ease}.post__title:hover{background-color:#004FEC;color:#fff}.link-bloc{margin-top:auto;display:flex;align-items:center}.link-bloc__date{font-size:20px;line-height:24px;color:rgba(51,61,81,0.5)}.link-bloc__date span{margin-right:7px;font-size:30px;line-height:37px;color:#333D51}

.footer{padding:45px 0;display:flex;justify-content:space-between;align-items:center}.block-title__title,.developed__title{font-weight:bold;font-size:25px;line-height:30px}.footer-project{font-size:15px;line-height:18px;text-align:center}.developed{text-align:right}.block-title__subtitle,.developed__subtitle{font-size:12px;line-height:15px}

.contact-wrapper{display:flex;justify-content:space-between}.block-img{max-width:540px;height:340px}.block-data__number{font-size:60px;font-weight:normal;line-height:93%}.block-data__number a{color:#004FEC}.block-data__name{margin-top:50px;padding-left:25px;font-size:25px;line-height:30px;width:150px}.block-data__name:before{left:0px}.block-data__mail{padding-left:25px;font-size:20px;line-height:24px;margin-top:25px}.block-data__skills{padding-left:25px;font-size:20px;line-height:24px;color:rgba(51,61,81,0.5);margin-top:50px}.block-data__skills-subtitle{padding-left:25px;font-size:20px;line-height:24px;color:#333D51;margin-top:25px}.block-data__skills-subtitle span{color:#004FEC}

.wrapper-news{display:flex}.block-posts__title{text-transform:none;text-transform:initial;font-style:normal;font-weight:normal;font-size:60px;line-height:73px;margin-bottom:30px;max-width:450px;margin-right:30px}.block-posts__title span{color:#004FEC}.block-posts__link{font-size:25px;line-height:30px;color:rgba(51,61,81,0.5)}.block-posts__date{margin-top:30px;font-size:30px;line-height:37px}.block-posts__date span{font-size:20px;line-height:24px;color:rgba(51,61,81,0.5)}.article{max-width:730px}.article img{width:100%}article p.text{margin-top:30px}

